"use client"

import { useState } from "react"
import { gifts } from "../data/gifts"

export default function GiftList() {
  const [loaded, setLoaded] = useState({})

  return (
    <section className="bg-white rounded-3xl shadow-md p-10 md:col-span-2 hover:shadow-xl transition duration-300 text-center">
      <h2 className="text-2xl font-semibold text-blue-800 mb-6">üéÅ Lista de Presentes</h2>

      <p className="text-blue-700 mb-4">
        Caso queira nos presentear, criamos uma lista com itens √∫teis para nosso lar.
      </p>

      <div className="max-h-[420px] overflow-y-auto pr-3 mx-auto scrollbar-thin 
                      scrollbar-thumb-blue-300 scrollbar-track-blue-100 rounded-xl">

        <ul className="grid md:grid-cols-2 gap-5 text-left">

          {gifts.map((item, idx) => (
            <li key={idx} className="flex gap-4 bg-blue-50 p-3 rounded-xl shadow-sm hover:shadow-md transition">

              {/* Wrapper com placeholder */}
              <div className="w-20 h-20 rounded-lg overflow-hidden relative">

                {!loaded[idx] && (
                  <div className="absolute inset-0 bg-blue-100 animate-pulse"></div>
                )}

                <img
                  src={item.img}
                  alt={item.name}
                  loading="lazy"
                  onLoad={() =>
                    setLoaded((prev) => ({ ...prev, [idx]: true }))
                  }
                  className={`w-full h-full object-cover transition-opacity duration-700 ${
                    loaded[idx] ? "opacity-100" : "opacity-0"
                  }`}
                />
              </div>

              <div>
                <p className="font-semibold text-blue-900">{item.name}</p>
                <p className="text-blue-700 text-sm">{item.desc}</p>
              </div>
            </li>
          ))}

        </ul>
      </div>

      <p className="text-blue-600 italic mt-6">
        Sua presen√ßa j√° √© o maior presente ‚Äî mas se quiser contribuir, ficaremos imensamente felizes üíô
      </p>
    </section>
  )
}
